.header-container {
  height: 72px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

@media screen and (min-width: 1024px) {
  .header-container {
    height: 80px;
  }
}

.header-title {
  color: var(--dark-text-color);
  height: 38px;
  font-family: Stalemate, sans-serif;
  font-size: 42px;
  font-weight: normal;
  line-height: 1.36;
  display: inline-block;
}

@media screen and (min-width: 1024px) {
  .header-title {
    height: 52px;
    font-size: 58px;
    line-height: 1.38;
  }
}

.header-title span {
  color: var(--primary-color);
  height: inherit;
  display: inline-block;
}

.burger-btn {
  background-color: rgba(0, 0, 0, 0);
  border: rgba(0, 0, 0, 0);
}

.burger-btn:focus-visible {
  display: none;
}

@media screen and (min-width: 1024px) {
  .burger-btn {
    display: none;
  }
}

.icon-menu {
  stroke: #00f;
  width: 40px;
  height: 40px;
  transition: var(--transition-dur-and-func);
  justify-content: center;
  align-items: center;
  display: flex;
}

.burger-btn:active .icon-menu {
  stroke: #00f;
}

.menu-list {
  display: none;
}

@media screen and (min-width: 1024px) {
  .menu-list {
    color: var(--dark-text-color);
    column-gap: 56px;
    font-size: 19px;
    font-weight: 500;
    line-height: 1.16;
    display: flex;
  }
}

.menu-link {
  transition: var(--transition-dur-and-func);
}

.menu-link:-webkit-any(:hover, :focus) {
  color: var(--first-text-hover);
}

.menu-link:is(:hover, :focus) {
  color: var(--first-text-hover);
}

.menu-link:active {
  color: var(--primary-color);
}

.order-now-modal-close {
  width: 32px;
  height: 32px;
  stroke: #000;
  transition: stroke var(--transition-dur-and-func);
}

.order-now-modal-close:hover {
  stroke: red;
}

.icon-x {
  stroke: green;
  transition: var(--transition-dur-and-func);
  justify-content: center;
  align-items: center;
  display: flex;
}

